<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('INVOICE_REFERENCE') }}:</span>
          <span>{{ loan.invoiceNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LOAN_TYPE') }}:</span>
          <span>{{ loan.typeName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LOAN_ACCOUNT_NUMBER') }}:</span>
          <span>{{ loan.loanNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="loan.settlementAccountNumber">
          <span>{{ $t('SETTLEMENT_ACCOUNT_NUMBER') }}:</span>
          <span>{{ loan.settlementAccountNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('EFFECTIVE_DATE') }}:</span>
          <span>{{ loan.startDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DUE_DATE') }}:</span>
          <span>{{ loan.maturityDate | formatUnixDate }}</span>
        </p>
      </div>
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('CURRENCY') }}:</span>
          <span>{{ loan.currencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('PRINCIPAL_AMOUNT') }}:</span>
          <span>{{ loan.loanAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('REMAINING_AMOUNT') }}:</span>
          <span>{{ loan.outstandingAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('INTEREST_RATE') }}:</span>
          <span>{{ loan.interestRate | placeholder('-') }}%</span>
        </p>
        <p class="info-row">
          <span>{{ $t('INTEREST_AMOUNT') }}:</span>
          <span>{{ loan.interestAmount | currency('') | placeholder('-') }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import TradeFinanceService from '@/services/trade/TradeFinanceService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'

export default {
  name: 'sales-invoice-loan-outstanding-view',
  mixins: [AlertMixin, LoadingMixin],
  data: function () {
    return {
      loan: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      TradeFinanceService.outstandingSalesInvoiceLoans({
        loanNumber: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.loan = response.data.loanList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
